import React, { Component } from "react";

class About extends Component {
  render() {
    return (
      <div className="widget widget-about">
        <p></p>
        <p>世风之狡诈多端, 到底忠厚人颠扑不破.</p>
        <p>末俗以繁华相尚, 只觉冷淡处趣味弥长.</p>
      </div>
    );
  }
}
export default About;



